

/** DESTINATIONS GRID  **/

.destinations-grid_text {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.destinations-grid {
  display: grid;
  grid-template-columns: 1fr 1fr ;
  gap: 20px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.destinations-grid_text p button {
  background-color: #fa8900;
}

/** GRID  **/

.grid-card {
  background-color: var(--white);
  border-radius: var(--border-radius-15);
  border: 2px solid var(--notion-dark);
  padding: 1rem;
  width: 33%;

}

.grid-card__links {
  display: flex;
  justify-content: space-between;
}

.grid-card__img {
  padding-bottom: 1rem;
}

.grid-card__img img {
  border-radius: var(--border-radius-15);
  aspect-ratio: 3 / 2;
  object-fit: cover;
  border: 2px solid var(--notion-dark);
}

.grid-card__text-container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: var(--white);
  border: 2px solid var(--notion-dark);
  border-radius: var(--border-radius-15);

}

.grid-card__wrapper {
  display: flex;
}

.grid-card__text {
  display: flex;
  gap: 0.4rem;
  flex-direction: column;
}

.grid-card__text p {
  /* font-size: var(--font-size-md); */
}

